import axios from 'axios'
import jsonp from 'common/js/jsonp'

export function getLyric(mid) {
  const url = '/api/lyric'
  const data = {
    pcachetime: +new Date(),
    songmid: mid,
    g_tk: 66948055,
    loginUin: 1096246853,
    hostUin: 0,
    format: 'jsonp',
    inCharset: 'utf8',
    outCharset: 'utf-8',
    notice: 0,
    platform: 'yqq',
    needNewCode: 0,
    param: 'jsonpCallback'

  }
  return axios.get(url, {
      params: data
    })
    .then(res => {
      return Promise.resolve(res.data)
    })
}

export function getKey(mid) {
  const t = new Date().getUTCMilliseconds()
  const guid = (Math.round(2147483647 * Math.random()) * t) % 1e10
  const url = 'https://c.y.qq.com/base/fcgi-bin/fcg_music_express_mobile3.fcg'
  const data=Object.assign({},{
    g_tk: 5381,
    loginUin: 1096246853,
    inCharset: 'utf-8',
    outCharset: 'utf-8',
    notice: 0,
    format: 'json',
    cid: 205361747,
    platform: 'yqq',
    hostUin: 0,
    needNewCode: 0,
    uin: 1096246853,
    songmid: mid,
    filename: `C400${mid}.m4a`,
    guid: 1342169472,
    param: 'jsonpCallback'
})
  return jsonp(url, data)
}



