<template>
  <div class="song-list">
    <ul>
      <li @click="selectItem(song, index)" class="item" v-for="(song, index) in songs">
         <!--<div class="rank" v-show="rank">-->
           <!--<span :class="getRankCls(index)" v-text="getRankText(index)"></span>-->
         <!--</div>-->
        <div class="content">
          <h2 class="name">{{song.name}}</h2>
          <p class="desc">{{getDesc(song)}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: {
      songs: {
        type: Array,
        default: []
      },
//      rank: {
//        type: Boolean,
//        default: false
//      }
    },
    methods: {
      selectItem(item, index) {
        this.$emit('select', item, index)
      },
      getDesc(song) {
        return `${song.singer}·${song.album}`
      },
//      getRankCls(index) {
//        if (index <= 2) {
//          return `icon icon${index}`
//        } else {
//          return 'text'
//        }
//      },
//      getRankText(index) {
//        if (index > 2) {
//          return index + 1
//        }
//      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .song-list .item{
      display: flex;
      align-items: center;
      box-sizing: border-box;
      height: 0.64rem;
      font-size:0.14rem;
  }


  .song-list .item .rank{
flex: 0 0 0.25rem;
        width: 0.25rem;
        margin-right: 0.3rem;
        text-align: center;
      }
      .song-list .item .rank  .icon{
display: inline-block;
          width:0.25rem;
          height: 0.24rem;
          background-size:0.25rem 0.24rem;
      }

        .song-list .item .rank .icon0{
            bg-image('first');
        }
         .song-list .item .rank .icon1{
            bg-image('second');
        }
         .song-list .item .rank .icon2{
            bg-image('third');
        }

      .song-list .item .rank  .text{
           color: #fff;
          font-size: 0.14erm;
      }

     .song-list .item .content{
  flex: 1;
        line-height: 0.2rem;
        overflow: hidden;
     }

      .song-list .item .content   .name{
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
          color: #fff;
          font-size:0.15rem;
          font-weight:normal;
      }

      .song-list .item .content   .desc{
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
          margin-top: 4px;
          color:rgba(255, 255, 255, 0.3);
          font-size:0.12rem;
      }

</style>



