<template>
  <div class="player" v-show="playlist.length>0">
    <transition name="normal"
                @enter="enter"
                @after-enter="afterEnter"
                @leave="leave"
                @after-leave="afterLeave"
    >
    <div class="normal-player" v-show="fullScreen">
      <div class="background">
        <img width="100%" height="100%" :src="currentSong.image">
      </div>
      <div class="top">
        <div class="back" @click="back">
          <i class="icon-back"></i>
        </div>
        <h1 class="title" v-html="currentSong.name"></h1>
        <h2 class="subtitle" v-html="currentSong.singer"></h2>
      </div>
      <div class="middle">
        <div class="middle-l">
          <div class="cd-wrapper" ref="cdWrapper">
            <div class="cd" :class="cdCls">
              <img src="" alt="" class="image" :src="currentSong.image">
            </div>
          </div>
        </div>
      </div>
      <div class="bottom">
        <div class="progress-wrapper">
          <span class="time time-l">{{format(currentTime)}}</span>
          <div class="progress-bar-wrapper">
            <progress-bar :percent="percent" @percentChange="onPercentChange"></progress-bar>
          </div>
          <span class="time time-r">{{format(currentSong.duration)}}</span>
        </div>
        <div class="operators">
          <div class="icon i-left" @click="changeMode">
            <i :class="iconMode"></i>
          </div>
          <div class="icon i-left" :class="disableCls">
            <i class="icon-prev" @click="prev"></i>
          </div>
          <div class="icon i-center" :class="disableCls">
            <i :class="playIcon" @click="togglePlaying"></i>
          </div>
          <div class="icon i-right" :class="disableCls">
            <i class="icon-next" @click="next"></i>
          </div>
          <div class="icon i-right" >
            <i class="icon icon-not-favorite"></i>
          </div>
        </div>
      </div>
    </div>
    </transition>
    <transition name="mini">
    <div class="mini-player" v-show="!fullScreen" @click="open">
      <div class="icon">
        <img :class="cdCls" width="40" height="40" alt="" :src="currentSong.image">
      </div>
      <div class="text">
        <h2 class="name" v-html="currentSong.name"></h2>
        <p class="desc" v-html="currentSong.singer"></p>
      </div>
      <div class="control">
        <progress-circle :radius="radius" :percent="percent">
          <i :class="miniIcon " class="icon-mini"  @click.stop="togglePlaying"></i>
        </progress-circle>
      </div>
      <div class="control">
        <i class="icon-playlist"></i>
      </div>
    </div>
    </transition>
    <audio @timeupdate="updateTime" ref="audio" :src="currentSong.url" @canplay="ready" @error="error" @ended="end"></audio>
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapGetters,mapMutations} from 'vuex'
  import animations from 'create-keyframe-animation'
  import {prefixStyle} from 'common/js/dom'
  import ProgressBar from 'base/progress-bar/progress-bar'
  const transform =prefixStyle('transform')
  import {playMode} from 'common/js/config'
  import {shuffle} from 'common/js/util'
  import ProgressCircle from 'base/progress-circle/progress-circle'
  export default {
    data(){
      return{
        songReady:false,
        currentTime:0,
        radius:32
      }
    },
    components:{
      ProgressBar,
      ProgressCircle
    },
    computed:{
      iconMode(){
        return this.mode===playMode.sequence? 'icon-sequence':this.mode===playMode.loop?'icon-loop':'icon-random'
      },
      cdCls(){
        return this.playing? 'play':'pause'
      },
      playIcon(){
        return this.playing? 'icon-pause':'icon-play'
      },
      miniIcon(){
        return this.playing? 'icon-pause-mini':'icon-play-mini'
      },
      disableCls(){
        return this.songReady? '':'disable'
      },
      percent(){
          return this.currentTime/this.currentSong.duration
      },
      ...mapGetters([
        'fullScreen',
        'playlist',
        'currentSong',
        'playing',
        'currentIndex',
        'mode',
        'sequenceList'
      ])
    },
    watch:{
      currentSong(newSong,oldSong){
        if(newSong.id===oldSong.id){
          return
        }
        this.$nextTick(()=>{
          this.$refs.audio.play()
          this.currentSong.getLyric()
        })
      },
      playing(newPlaying){
        const audio=this.$refs.audio
        this.$nextTick(()=>{
        newPlaying?audio.play():audio.pause()
        })
      }
    },
    methods:{
      changeMode(){
        const mode=(this.mode+1)%3;
        this.setPlayMode(mode);
        let list=null;
        if(mode===playMode.random){
          list=shuffle(this.sequenceList)
        }else{
          list=this.sequenceList
        }
        this.resetCurrentIndex(list)
        this.setPlaylist(list)
      },
      resetCurrentIndex(list){
        let index=list.findIndex((item)=>{
          return item.id===this.currentSong.id
        })
        this.setCurrentIndex(index)
      },
      back(){
        this.setFullScreen(false);
      },
      open () {
        this.setFullScreen(true)

      },
      enter(el, done) {
        const {x, y, scale} = this._getPosAndScale()

        let animation = {
          0: {
            transform: `translate3d(${x}px,${y}px,0) scale(${scale})`
          },
          60: {
            transform: `translate3d(0,0,0) scale(1.1)`
          },
          100: {
            transform: `translate3d(0,0,0) scale(1)`
          }
        }

        animations.registerAnimation({
          name: 'move',
          animation,
          presets: {
            duration: 400,
            easing: 'linear'
          }
        })

        animations.runAnimation(this.$refs.cdWrapper, 'move', done)
      },
      afterEnter() {
        animations.unregisterAnimation('move')
        this.$refs.cdWrapper.style.animation = ''
      },
      leave(el, done) {
        this.$refs.cdWrapper.style.transition = 'all 0.4s'
        const {x, y, scale} = this._getPosAndScale()
        this.$refs.cdWrapper.style[transform] = `translate3d(${x}px,${y}px,0) scale(${scale})`
        this.$refs.cdWrapper.addEventListener('transitionend', done)
      },
      afterLeave(){
        this.$refs.cdWrapper.style.transition=''
        this.$refs.cdWrapper.style[transform]='';
      },
      togglePlaying(){
        if(!this.songReady){
          return
        }
        this.setPlayingState(!this.playing)
      },
      end(){
        if(this.mode===playMode.loop){
          this.loop()
        }else{
          this.next()
        }
      },
      loop(){
        this.$refs.audio.currentTime=0
        this.$refs.audio.play()
      },
      next(){
        if(!this.songReady){
          return
        }
        let index=this.currentIndex+1
        if(index===this.playlist.length){
          index=0
        }
        this.setCurrentIndex(index)
        if(!this.playing){
          this.togglePlaying()
        }
        this.songReady=false
      },
      prev(){
        if(!this.songReady){
          return
        }
        let index=this.currentIndex-1
        if(index===-1){
          index=this.playlist.length-1
        }
        this.setCurrentIndex(index)
        if(!this.playing){
          this.togglePlaying()
        }
        this.songReady=false
      },
      ready(){
        this.songReady=true
      },
      error(){
        this.songReady=true
      },
      updateTime(e){
        this.currentTime=e.target.currentTime
      },
      format(interval){
        interval=interval|0
        const minute=interval/60|0
        const second=this._pad(interval%60,2)
        return `${minute}:${second}`
      },
      _pad(num,n=2){
        //用0补位
        let len=num.toString().length;
        while(len<n){
          num='0'+num
          len++
        }
        return num
      },
      _getPosAndScale(){
        const targetWidth=40;
        const paddingLeft=40;
        const paddingBottom=30;
        const paddingTop=80;
        const width=window.innerWidth*0.8;
        const scale=targetWidth/width;
        const x = -(window.innerWidth/2-paddingLeft);
        const y=window.innerHeight-paddingTop-width/2-paddingBottom
        return {
          x,
          y,
          scale
        }
      },
      onPercentChange(percent){
          this.$refs.audio.currentTime=this.currentSong.duration*percent
        if(!this.playing){
          this.togglePlaying()
        }
      },
      ...mapMutations({
        setFullScreen:'SET_FULL_SCREEN',
        setPlayingState:'SET_PLAYING_STATE',
        setCurrentIndex:'SET_CURRENT_INDEX',
        setPlayMode:'SET_PLAY_MODE',
        setPlaylist:'SET_PLAYLIST'
      })

    }
  }
</script>

<style>
  .player .normal-player{
    position: fixed;
    left:0;
    top:0;
    bottom:0;
    right:0;
    z-index: 150;
    background: #222;
  }
  .player .normal-player .background{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0.6;
    filter: blur(20px);
  }

  .player .top{
    position: relative;
    margin-bottom: 0.25rem;
  }
  .player .top .back{
    position:absolute;
    top: 0;
    left: 0.06rem;
    z-index: 50;
  }

  .player .top .back .icon-back{
    display: block;
    padding: 0.09rem;
    color:#ffcd32;
    font-size:0.22rem;
    transform: rotate(-90deg);
  }

  .player .top .title{
    width: 70%;
    margin: 0 auto;
    line-height: 0.4rem;
    text-align: center;
    font-size: 0.18rem;
    color:#fff;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;

  }
  .player .top .subtitle{
    line-height: 0.2rem;
    text-align: center;
    font-size: 0.14rem;
    color: #fff;
  }






  .player .middle{
    position: fixed;
    width: 100%;
    top: 80px;
    bottom: 1.7rem;
    white-space: nowrap;
    font-size: 0;
  }

  .player .middle-l{
    display: inline-block;
    vertical-align: top;
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 80%;
  }

  .cd-wrapper{
    position: absolute;
    left: 10%;
    top: 0;
    width: 80%;
    height: 100%;
  }
  .cd-wrapper .cd{
    width: 100%;
    height: 100%;
  }
  .cd-wrapper .cd.play{
    animation: rotate 20s linear infinite;
  }
  .cd-wrapper .cd.pause{

    animation-fill-mode : forwards;
    animation-play-state: paused;

  }

  .cd-wrapper .cd  .image{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 50% ;
    box-sizing: border-box;
    border: 10px solid rgba(255, 255, 255, 0.1);
  }


  .playing-lyric-wrapper{
    width: 80%;
    margin: 0.3rem auto 0 auto;
    overflow: hidden;
    text-align: center;
  }

  .playing-lyric{
    height: 20px;
    line-height: 20px;
    font-size: 0.18rem;
    color:rgba(255, 255, 255, 0.5);
  }

  .middle-r{
    display: inline-block;
    vertical-align: top;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  .lyric-wrapper{
    width: 80%;
    margin: 0 auto;
    overflow: hidden;
    text-align: center;
  }

  .middle-r .lyric-wrapper .text{
    line-height: 0.32rem;
    color:rgba(255, 255, 255, 0.5);
    font-size: 0.18rem;
  }

  .middle-r .lyric-wrapper .text.current{
    color: #fff;
  }

  .bottom{
    position: absolute;
    bottom: 0.5rem;
    width: 100%;
  }

  .dot-wrapper{
    text-align: center;
    font-size: 0;
  }

  .dot-wrapper .dot{
    display: inline-block;
    vertical-align: middle;
    margin: 0 0.04rem;
    width: 0.08rem;
    height: 0.08rem;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.5);
  }

  .dot-wrapper  .dot.active{
    width: 0.2rem;
    border-radius: 5px;
    background: rgba(255, 255, 255, 0.8);
  }

  .progress-wrapper{
    display: flex;
    align-items: center;
    width: 80%;
    margin: 0px auto;
    padding: 10px 0 ;
  }

  .progress-wrapper .time{
    color:#fff;
    font-size: 0.12rem;
    flex: 0 0 0.3rem;
    line-height: 0.3rem;
    width: 0.3rem;
  }

  .progress-wrapper .time.time-l{
    text-align: left;
  }
  .progress-wrapper .time.time-r{
    text-align: right;
  }
  .progress-bar-wrapper{
    flex: 1;

  }
  .operators{
    display: flex;
    align-items: center;
  }

  .operators .icon{
    flex: 1;
    font-size:0.26rem;
    color: #ffcd32;
  }

  .operators .icon.disable{

    color: rgba(255, 205, 49, 0.5)
  }
  .operators .icon.disable i{

    /*font-size: 0.3rem;*/
  }
  .operators .i-left{
    text-align: right;

  }
  .operators .i-center{
    padding: 0 20px;
    text-align: center
  }

  .operators .i-center  i{

    font-size: 40px
  }
  .operators .i-right{
    text-align: left

  }
  .operators .icon-favorite{
    color: #d93f30
  }


  .normal-player.normal-enter-active, .normal-player.normal-leave-active{
    transition: all 0.4s;
  }
  .normal-player.normal-enter-active, .normal-player.normal-leave-active .top, .normal-player.normal-enter-active, .normal-player.normal-leave-active .bottom{
    transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32);

  }
  .normal-player.normal-enter, .normal-player.normal-leave-to{
    opacity: 0;

  }
  .normal-player.normal-enter .top, .normal-player.normal-leave-to .top{
    transform: translate3d(0, -1rem, 0);

  }
  .normal-player.normal-enter .bottom,.normal-player.normal-leave-to .bottom{
    transform: translate3d(0, 1rem, 0);
  }

  .mini-player{
    display: flex;
    align-items: center;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 180;
    width: 100%;
    height: 0.6rem;
    background: #333;
  }

  .mini-player.mini-enter-active, .mini-player.mini-leave-active{
    transition: all 0.4s;
  }
  .mini-player.mini-enter, .mini-player.mini-leave-to{
    opacity: 0;
  }
  .mini-player .icon{
    flex: 0 0 0.4rem;
    width: 0.4rem;
    padding: 0 0.1rem 0 0.2rem;
  }

  .mini-player .icon  img{
    border-radius: 50%;
  }
  .mini-player .icon  img.play{
    animation: rotate 10s linear infinite;
  }
  .mini-player .icon  img.pause{
    animation-play-state: paused;
    animation-fill-mode: forwards;
  }
  .mini-player .text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
    line-height: 0.2rem;
    overflow: hidden;
  }

  .mini-player .text  .name{
    margin-bottom: 2px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-size:0.14rem;
    color: #fff;
  }

  .mini-player .text  .desc{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-size: 0.12rem;
    color:rgba(255, 255, 255, 0.3);
  }

  .mini-player .control{
    flex: 0 0 30px;
    width: 30px;
    padding: 0 10px;
  }

  .icon-play-mini, .icon-pause-mini, .icon-playlist{
    font-size: 0.3rem;
    color:rgba(255, 205, 49, 0.5);
  }

  .icon-mini{
    font-size: 0.32rem;
    position: absolute;
    left: 0;
    top: 0;
  }


  @keyframes rotate{
    0%{
      transform: rotate(0);
    }
    100%{
      transform: rotate(360deg);
    }
  }
</style>



