<template>
     <div class="recommend">
         <scroll class="recommend-content" :data="discList" ref="scroll">
          <div>          
             <div class="slider-wrapper" v-if="recommends.length">
                <slider>
                    <div v-for="item in recommends" class="slider-item">
                        <a :href="item.linkUrl">
                            <img class="needsclick" :src="item.picUrl" alt="" @load="loadImage">
                        </a>
                    </div>
                </slider>
             </div>
             <div class="recommend-list">
              <h1 class="list-title">热门歌单推荐</h1>
              <ul>
                <li @click="selectItem(item)" v-for="item in discList" class="item">
                  <div class="icon">
                    <img  v-lazy="item.imgurl">
                  </div>
                  <div class="text">
                    <h2 class="name" v-html="item.creator.name"></h2>
                    <p class="desc" v-html="item.dissname"></p>
                  </div>
                </li>
              </ul>
          </div>
          <div class="loading-container" v-show="!discList.length">
              <loading> </loading>
          </div>
        </div>  
     </scroll>
  </div>
    
</template>

<script  type="text/ecmascript-6">
import Slider from "base/slider/slider";
import Scroll from "base/scroll/scroll";
import { getRecommend, getDiscList } from "api/recommend";
import { ERR_OK } from "api/config";
import Loading from "base/loading/loading"
export default {
  data() {
    return {
      recommends: [],
      discList: []
    };
  },
  created() {
    this._getRecommend(); 
    this._getDiscList();    
  },
  methods: {
    _getRecommend() {
      getRecommend().then(res => {
        if (res.code === ERR_OK) {
          console.log(res.data.slider);
          this.recommends = res.data.slider;
        }
      });
    },
    _getDiscList() {
      getDiscList().then(res => {
        if (res.code === ERR_OK) {
          console.log(res.data.list);
          this.discList = res.data.list;
        }
      });
    },
    loadImage(){
      // 确保执行一次
      if(!this.checkLoaded){
        this.$refs.scroll.refresh()
        this.checkLoaded=true
      }
    }
  },
  components: {
    Slider,
    Scroll,
    Loading
  }
};
</script>
<style>
.recommend {
  position: fixed;
  width: 100%;
  top: 0.88rem;
  bottom: 0;
}

.recommend-content {
  height: 100%;
  overflow: hidden;
  background:#222;  
}

.slider-wrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
}
.recommend-list{
  background:#222;

}
.recommend-list .list-title {
  height: 0.65rem;
  line-height: 0.65rem;
  text-align: center;
  font-size: 0.14rem;
  color: #FFCD32;
}
.recommend-list .item {
  display: flex;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  align-items: center;
  padding: 0 0.2rem 0.2rem 0.2rem;
}
.recommend-list .icon {
  flex: 0 0 0.6rem;
  width: 0.6rem;
  height: 0.6rem;
  padding-right: 0.2rem;
  background: transparent !important;
}
.recommend-list .icon img {
  width: 0.6rem;
  height: 0.6rem;
}
.recommend-list .text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
  line-height: 0.2rem;
  overflow: hidden;
  font-size: 0.12rem;
}
.recommend-list .name {
  margin-bottom: 0.1rem;
  color: #fff;
}
.recommend-list .desc {
  color: #999;
}
.recommend-list .loading-container {
  position: absolute;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
}
</style>






