<template>
  <div class="loading">
    <img width="24" height="24" src="./loading.gif">
    <p class="desc">{{title}}</p>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    props: {
      title: {
        type: String,
        default: '正在载入...'
      }
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">

  .loading{
    width: 100%;
    text-align: center;  
  }
    
    .desc{
      line-height: 0.2rem;
      font-size: 0.12rem;
      color:#fff;
      margin-top:0.1rem;
    }
      
</style>



